import React from 'react';
import { Card, CardActions, CardContent, CardMedia, Button, Typography } from '@material-ui/core/';
import moment from 'moment';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import DeleteIcon from '@material-ui/icons/Delete';
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';

import { useDispatch } from 'react-redux';
import { deletePost, likePost } from '../../../context/actions/postsAction.js';

import useStyles from './styles.js';

export default function SinglePost({ post, setCurrentId }) {
    const classes = useStyles();
    const dispatch = useDispatch();
    const user = JSON.parse(localStorage.getItem('profile'));

    return (
        <Card className={classes.card}>
            <CardMedia className={classes.media} image={post.selectedFile} title={post.title} />
            <div className={classes.overlay}>
                <Typography variant="h6">{post.name}</Typography>
                <Typography variant="body2">{moment(post.createdAt).fromNow()}</Typography>
            </div>
            {(user?.result?.googleId === post?.creator || user?.result?._id === post?.creator) ?
                <div className={classes.overlay2}>
                    <Button style={{ color: 'white' }} size="small" onClick={() => setCurrentId(post._id)} ><MoreHorizIcon fontSize="medium" /></Button>
                </div>
                : null}

            <div className={classes.details}>
                <Typography variant="body2" color="textSecondary" component="h2">{post.tags.map((tag) => `#${tag.trim()} `)}</Typography>
            </div>
            <Typography className={classes.title} gutterBottom variant="h5" component="h2">{post.title}</Typography>
            <CardContent>
                <Typography color="textSecondary" variant="body2" component="p">{post.message}</Typography>
            </CardContent>
            <CardActions className={classes.cardActions}>
                <Button size="small" color="primary" disabled={!user?.result} onClick={() => dispatch(likePost(post._id))} ><ThumbUpAltIcon fontSize="small" /> &nbsp; {post.likes.length}</Button>
                {(user?.result?.googleId === post?.creator || user?.result?._id === post?.creator) ?
                    <Button size="small" color="primary" disabled={!user?.result} onClick={() => dispatch(deletePost(post._id))} ><DeleteIcon fontSize="small" /></Button>
                    : null}
            </CardActions>
        </Card>
    )
}